<style lang="scss" scoped>
.list-group{  
  width: 800px;
  .col-group{
    margin-top: 20px;
    cursor: pointer;
    
    .card-group{
      height: 180px;
      width: 100%;
      .thumbnail{
        width: 100%;
      }
      .title{
        padding-top: 10px;
      }
      &:hover{
        box-shadow: 0px 0 1px rgba(0, 0, 0, 0.4);
      }
    }
    .add-card{
      font: bolder 100px '微软雅黑';
      display: flex;
      text-align: center;
      justify-content: center;
      color: #BBBEC7;
    }
  }
  .pagination{
    text-align: right;
    padding-top: 20px;
  }
}
</style>

<template>
  <div class="merchant-container">
    <h1>商家列表</h1>
    <div class="list-group">
      <el-row :gutter="20">
        <!-- 添加按钮 -->
        <el-col :span="6" class="col-group">
          <router-link to="/merchant/detail">
            <el-card class="card-group add-card">+</el-card>
          </router-link>
        </el-col>
        <!-- 商家 -->
        <el-col :span="6" class="col-group" v-for="merchant in merchants" :key="merchant.id">
          <router-link :to="'/merchant/detail?id='+merchant.id">
            <el-card class="card-group">
              <img :src="merchant.logo" class="thumbnail">
              <div class="title">{{merchant.name}}</div>
            </el-card>
          </router-link>
        </el-col>
      </el-row>
      <el-pagination
        class="pagination"
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="12"
        :current-page="page"
        @current-change="onCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import {Row,Col,Card,Pagination} from "element-ui"

export default {
  name: "Merchant",
  data: function(){
    return {
      merchants: [],
      total: 0,
      page: 1,  // 当前页码，默认为1
    }
  },
  components: {
    [Row.name]:Row,
    [Col.name]:Col,
    [Card.name]:Card,
    [Pagination.name]:Pagination
  },
  mounted(){
    // 获取第一页的merchants
    const page = 1
    this.getMerchants(page)
  },
  methods:{
    // 页码改变时执行
    onCurrentChange(page){
      this.getMerchants(page)
    },
    // 获取商家信息   page: 将要跳转到的页码
    getMerchants(page){
      this.$loading.show()
      this.$http.getMerchants(page).then(res => {
        const data = res.data
        this.merchants = data.results
        this.total = data.count
        // 获取到merchants后，更新页码page
        this.page = page
        this.$loading.hide()
      }).catch(err => {
        window.console.log(err)
        this.$loading.hide()
      })
    },
  }
}
</script>
