<style scoped>
.el-aside >>> .el-menu{
  border: none;
}
</style>

<style lang="scss" scoped>
.frame-container{
  height: 100vh;
  .header{
    background: #00a65a;
    display: flex;
    .brand{
      width: 200px;
      margin-left: -20px;
      background-color:#008d4c;
      font-size: 20px;
      color: #fff;
      display:flex;
      justify-content: center;
      align-items: center;
    }
    .header-content{
      flex: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-left: 20px;
      color: #fff;
      .signout{
        cursor: pointer;
      }
    }
  }
  .aside{
    background-color: #545c64;
    .el-menu{
      .is-active{
        background-color: #434a50!important;
      }
    }
  }
  .main{
    // background: #ff0;
  }
  .footer{
    background: gray;
  }
}
</style>

<template>
  <div class="frame">
    <el-container class="frame-container">
      <el-header class="header">
        <a href="/" class="brand"><strong>知了</strong>商户平台</a>
        <div class="header-content">
          <div class="greet">欢迎，{{$auth.user.username}}！</div>
          <div class="signout" @click="onLogout">退出登录</div>
        </div>
      </el-header>
      <el-container>
        <el-aside class="aside" width="200px">
          <el-row class="menu-row">
            <el-col :span="24">
              <el-menu
                default-active="/"
                background-color="#545c64"
                class="el-menu"
                active-text-color="#fff"
                text-color="#ddd"
                :router="true"
                >
                <el-menu-item index="/">
                  <i class="el-icon-s-home"></i>
                  <span slot="title">首页</span>
                </el-menu-item>
                <el-menu-item index="/merchant" v-if="is_edit || is_admin">
                  <i class="el-icon-menu"></i>
                  <span slot="title">商家</span>
                </el-menu-item>
                <el-menu-item index="/user" v-if="is_admin">
                  <i class="el-icon-user-solid"></i>
                  <span slot="title">用户</span>
                </el-menu-item>
                <el-menu-item index="/order" v-if="is_finance || is_admin">
                  <i class="el-icon-s-order"></i>
                  <span slot="title">订单</span>
                </el-menu-item>
              </el-menu>
            </el-col>
          </el-row>
        </el-aside>
        <el-container>
          <el-main class="main">
            <router-view></router-view>
          </el-main>
          <el-footer class="footer">Footer部分</el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script type="text/ecmascript-6">
import {Container,Header,Aside,Main,Footer,Row,Col,Menu,MenuItem} from "element-ui"

export default {
  name: "Frame",
  data: function(){
    return {
      is_edit: false,
      is_finance: false,
      is_admin: false
    }
  },
  components: {
    [Container.name]:Container,
    [Header.name]:Header,
    [Aside.name]:Aside,
    [Main.name]:Main,
    [Footer.name]:Footer,
    [Row.name]: Row,
    [Col.name]: Col,
    [Menu.name]: Menu,
    [MenuItem.name]: MenuItem
  },
  methods:{
    onLogout(){
      this.$auth.clearUserToken()
      this.$router.replace('/login')
    }
  },
  mounted(){
    const groups = this.$auth.user.groups
    for(let group of groups){
      if(group.name == '编辑'){
        this.is_edit = true
      } else if(group.name == '财务'){
        this.is_finance = true
      } else if(group.name == '管理'){
        this.is_admin = true
      }
    }
    if(this.$auth.user.is_superuser){
      this.is_admin = true
    }
  }
}
</script>

