import { Message } from "element-ui"

class MTMessage{
  constructor(){
    this.config = {
      showClose: true,
      duration: 2000
    }
  }
  show(){
    /**
     * 如果多次调用Message，并且多次使用相同的config，则提示消息弹出后不会消失（bug）
     * 所以要重新生成一个config对象
     */
    Message(JSON.parse(JSON.stringify(this.config)))
  }
  success(message = "恭喜，操作成功！"){
    this.config.type = "success"
    this.config.message = message
    this.show()
  }
  warning(message = ""){
    this.config.type = "warning"
    this.config.message = message
    this.show()
  }
  info(message = ""){
    this.config.type = "info"
    this.config.message = message
    this.show()
  }
  error(message = ""){
    this.config.type = "error"
    this.config.message = message
    this.show()
  }
}

const message = new MTMessage()
export default message