<style scoped>
.logo-upload >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  background-color: rgba(255,255,255,0.5);
}
.logo-upload >>> .el-upload:hover {
  border-color: #409eff;
}
</style>

<style lang="scss" scoped>
.form-group{
  padding-top: 20px;
  width: 460px;
}
.logo-upload{
  .logo {
    width: 178px;
    height: 133px;
    display: block;
  }
  .logo-upload-icon{
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 133px;
    line-height: 133px;
    text-align: center;
  }
}

.category-list{
  width: 800px;
  .category-group{
    .category-box{
      padding: 10px;
      box-sizing: border-box;
    }
    .add-category{
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }
    .category-info{
      background-color: rgb(217, 236, 255);
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .goods-list{
      .goods-group{
        font-size: 14px;
        display: flex;
        justify-content: space-between;
        padding: 10px;
        margin: 10px 0;
        background-color: #ddd;
        &:hover{
          background-color: #ccc;
        }
        &:hover .ops-group{
          display: block;
        }
        .ops-group{
          display:none;
          font-size: 12px;
          justify-content: space-between;
          span{
            cursor: pointer;
          }
          .edit{
            margin-right: 10px;
          }
        }
      }
      .add-goods{
        background-color: #ddd;
        text-align: center;
        padding: 10px;
        cursor: pointer;
        margin-bottom: 20px;
      }
    }
  }
}
</style>

<template>
  <div class="">
    <h1>商家详情</h1>
    <el-tabs v-model="activeName">
      <!-- 基本信息 -->
      <el-tab-pane label="基本信息" name="1">
        <el-form ref="myForm" :model="myForm" :rules="rules" label-width="100px" class="form-group">
          <el-form-item label="店铺名称" prop="name">
            <el-input v-model="myForm.name"></el-input>
          </el-form-item>
          <el-form-item label="店铺地址" prop="address">
            <el-input v-model="myForm.address"></el-input>
          </el-form-item>
          <el-form-item label="店铺logo" prop="logo">
            <el-upload
              class="logo-upload"
              :action="upload_action"
              :headers="headers"
              :show-file-list="false"
              :on-success="onUploadSuccess"
              :before-upload="onBeforeUpload">
              <img v-if="myForm.logo" :src="myForm.logo" class="logo">
              <i v-else class="el-icon-plus logo-upload-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="店铺经度" prop="lon">
            <el-input v-model="myForm.lon"></el-input>
          </el-form-item>
          <el-form-item label="店铺纬度" prop="lat">
            <el-input v-model="myForm.lat"></el-input>
          </el-form-item>
          <el-form-item label="店铺公告" prop="notice">
            <el-input v-model="myForm.notice"></el-input>
          </el-form-item>
          <el-form-item label="起送价格" prop="up_send">
            <el-input v-model="myForm.up_send"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.prevent="onSubmit">提交</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <!-- 商品管理 -->
      <el-tab-pane label="商品管理" name="2">
        <ul class="category-list">
          <li class="category-group" v-for="(category,index) in categories" :key="category.id+category.name">
            <div class="category-info category-box">
              <span class="category-title">{{category.name}}</span>
              <div>
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="onEditCategory(category)">编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="onDeleteCategory(category,index)">删除</el-button>
              </div>
            </div>
            <ul class="goods-list">
              <li class="goods-group" v-for="(goods,goods_index) in category.goods_list" :key="goods.id+goods.name">
                <span class="goods-title">{{goods.name}}</span>
                <div class="ops-group">
                  <span class="edit" @click="onUpdateGoods(goods)">编辑</span>
                  <span class="delete" @click="onDeleteGoods(goods,goods_index,category)">删除</span>
                </div>
              </li>
              <li class="add-goods" style="font-weight:800" @click="onAddGoods(category)">+新增商品</li>
            </ul>
          </li>
          <li class="category-group">
            <div class="category-box add-category" @click="onAddCategory">+新增分类</div>
          </li>
        </ul>
      </el-tab-pane>
    </el-tabs>
    <!-- 添加/编辑分类对话框 -->
    <el-dialog title="添加/编辑分类" :visible.sync="visibleCategoryDialog">
      <el-form :model="categoryForm">
        <el-form-item label="分类名称" label-width="80px">
          <el-input v-model="categoryForm.name" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="visibleCategoryDialog=false">取 消</el-button>
        <el-button type="primary" @click="onSubmitCategory">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 添加/编辑商品对话框 -->
    <el-dialog title="新增/编辑商品" :visible.sync="visibleGoodsDialog" width="40%">
      <el-form :model="goodsForm" :rules="goodsRules" ref="goodsForm">
        <el-form-item label="商品名称" label-width="100px" prop="name">
          <el-input v-model="goodsForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品图片" label-width="100px" prop="picture">
          <el-upload
            class="logo-upload"
            :action="upload_action"
            :headers="headers"
            :show-file-list="false"
            :on-success="onGoodsPictureUploadSuccess"
            :before-upload="onBeforeUpload"
          >
            <img v-if="goodsForm.picture" :src="goodsForm.picture" class="logo" />
            <i v-else class="el-icon-plus logo-upload-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="商品价格" label-width="100px" prop="price">
          <el-input v-model="goodsForm.price" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="商品介绍" label-width="100px" prop="intro">
          <el-input v-model="goodsForm.intro" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="visibleGoodsDialog = false">取 消</el-button>
        <el-button type="primary" @click="onSubmitGoods">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import {Tabs,TabPane,Form,FormItem,Input,Upload,Button,Dialog,MessageBox} from "element-ui"

export default {
  name: "MerchantDetail",
  data: function(){
    return {
      activeName:"1",
      merchant_id: null,
      categories: [],
      myForm:{
        name: "",
        logo: "",
        address: "",
        notice: "",
        up_send: "0",
        lon: "",
        lat: ""
      },
      rules: {
        name: [{ required: true, message: "请输入商家名称！", trigger: "blur" }],
        address: [{ required: true, message: "请输入地址！", trigger: "blur" }],
        logo: [{ required: true, message: "请上传图片！", trigger: "blur" }],
        lon: [{ required: true, message: "请输入经度！", trigger: "blur" }],
        lat: [{ required: true, message: "请输入纬度！", trigger: "blur" }]
      },
      // 图片上传地址
      upload_action: "http://127.0.0.1:8000/cms/upload",
      // 上传图片请求头
      headers: {
        "AUTHORIZATION":"JWT " + this.$auth.token
      },
      visibleCategoryDialog: false,
      categoryForm: {
        id: "",
        name: "",
        merchant_id: ""
      },
      active_category: null,
      active_goods: null,
      visibleGoodsDialog: false,
      goodsForm: {
        id: '',
        name: '',
        picture: '',
        price: '',
        intro: '',
        category_id: ''
      },
      goodsRules: {
        name: [{ required: true, message: "请输入商品名称！", trigger: "blur" }],
        intro: [{ required: true, message: "请输入商品介绍！", trigger: "blur" }],
        picture: [{ required: true, message: "请上传图片！", trigger: "blur" }],
        price: [{ required: true, message: "请输入价格！", trigger: "blur" }]
      },
    }
  },
  methods:{
    initCategoryForm(){
      this.categoryForm = {
        id: "",
        name: "",
        merchant_id: ""
      }
    },
    initGoodsForm(){
      this.goodsForm = {
        id: '',
        name: '',
        picture: '',
        price: '',
        intro: '',
        category_id: ''
      }
    },
    // 复制
    copy(value){
      return JSON.parse(JSON.stringify(value))
    },
    // 上传前检查格式是否正确
    onBeforeUpload(file){
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        alert('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        alert('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    },
    // 上传成功后执行
    onUploadSuccess(res){
      this.myForm.logo = res.picture
    },
    // 提交
    onSubmit(){
      this.$refs['myForm'].validate(valid => {
        if(!valid){
          return
        }
        this.$loading.show()
        if(this.merchant_id){
          // 修改
          this.$http.updateMerchant(this.merchant_id,this.myForm).then(() => {
            this.$loading.hide()
            this.$router.push({name:'merchant'})
          }).catch(err => {
            window.console.log(err)
            this.$loading.hide()
          })
        } else{
          // 添加
          this.$http.addMerchant(this.myForm).then(() => {
            // params: res
            // const merchant = res.data
            this.$loading.hide()
            this.$router.push({name:'merchant'})
          }).catch(err => {
            window.console.log(err)
            this.$loading.hide()
          })
        }
      })
    },
    // 编辑分类
    onEditCategory(category){
      this.visibleCategoryDialog = true
      this.categoryForm = {
        id: category.id,
        name: category.name,
        merchant_id: category.merchant_id,
      }
      this.active_category = category
    },
    // 删除分类
    onDeleteCategory(category,index){
      if(category.goods_list.length > 0){
        this.$message.info("该分类下还有商品，不能删除！")
        return
      }
      this.$http.deleteCategory(category.id).then(() => {
        this.categories.splice(index,1)
        this.$message.success()
      }).catch(err => {
        this.$message.error(err)
      })
    },
    // 新增分类
    onAddCategory(){
      this.initCategoryForm()
      this.visibleCategoryDialog = true
    },
    // 提交分类
    onSubmitCategory(){
      if(this.categoryForm.id){
        // 编辑分类
        this.$http.updateCategory(this.categoryForm.id,this.categoryForm).then(() => {
          // 更新categories
          // for(let category of this.categories){
          //   if(category.id == this.categoryForm.id){
          //     category.name = this.categoryForm.name
          //     break
          //   }
          // }
          this.active_category.name = this.categoryForm.name
          this.visibleCategoryDialog = false
          this.$message.success()
        }).catch(err => {
          this.$message.error(err)
        })
      } else{
        // 添加分类
        this.categoryForm.merchant_id = this.merchant_id
        this.$http.addCategory(this.categoryForm).then(res => {
          this.visibleCategoryDialog = false
          this.$message.success()
          const category = res.data
          this.categories.push(category)
        }).catch(err => {
          this.$message.error(err)
        })
      }
    },
    // 新增商品
    onAddGoods(category){
      this.active_category = category
      this.goodsForm.category_id = category.id
      this.visibleGoodsDialog = true
    },
    // 商品图片上传成功钩子
    onGoodsPictureUploadSuccess(res){
      this.goodsForm.picture = res.picture
    },
    // 提交商品
    onSubmitGoods(){
      this.$refs['goodsForm'].validate(valid => {
        if(!valid){
          return
        }
        if(this.goodsForm.id){
          // 更新商品
          this.$http.updateGoods(this.goodsForm.id,this.goodsForm).then(res => {
            // const new_goods = res.data
            // for(let category of this.categories){
            //   const goods_list = category.goods_list
            //   for(let index=0; index<goods_list.length; index++){
            //     const goods = goods_list[index]
            //     if(goods.id == new_goods.id){
            //       goods_list[index] = this.copy(new_goods)
            //       this.visibleGoodsDialog = false
            //       this.$message.success()
            //       this.initGoodsForm()
            //       return
            //     }
            //   }
            // }

            const goods = res.data
            this.active_goods.name = goods.name
            this.active_goods.picture = goods.picture
            this.active_goods.price = goods.price
            this.active_goods.intro = goods.intro
            this.active_goods.category_id = goods.category_id
            this.active_goods = null
            this.visibleGoodsDialog = false
            this.$message.success()
            this.initGoodsForm()
          }).catch(err => {
            this.$message.error(err)
          })
        } else{
          // 新增商品
          this.$http.addGoods(this.goodsForm).then(res => {
            const goods = res.data
            // 把商品添加到categories
            this.active_category.goods_list.push(goods)
            this.visibleGoodsDialog = false
            this.$message.success()
            this.initGoodsForm()
          }).catch(err => {
            this.$message.error(err)
          })
        }
      })
    },
    // 编辑商品
    onUpdateGoods(goods){
      this.goodsForm = this.copy(goods)
      this.active_goods = goods
      this.visibleGoodsDialog = true
    },
    // 删除商品
    onDeleteGoods(goods,goods_index,category){
      MessageBox.confirm("您确定要删除该商品吗？",{
        title: '删除',
        type: 'warning',
        callback: (action) => {
          if(action == 'confirm'){
            this.$http.deleteGoods(goods.id).then(() => {
              this.$message.success()
              category.goods_list.splice(goods_index,1)
            }).catch(err => {
              this.$message.error(err)
            })
          }
        }
      })
    }
  },
  mounted(){
    const merchant_id = this.$route.query.id
    if(merchant_id){
      // 如果存在merchant_id
      this.merchant_id = merchant_id
      this.$loading.show()
      this.$http.getMerchant(merchant_id).then(res => {
        const merchant = res.data
        this.myForm = {
          name: merchant.name,
          logo: merchant.logo,
          address: merchant.address,
          notice: merchant.notice,
          up_send: merchant.up_send,
          lon: merchant.lon,
          lat: merchant.lat
        }
        this.$loading.hide()
      }).catch(err => {
        window.console.log(err)
        this.$loading.hide()
      })
      // 获取商品分类列表
      this.$http.getCategories(merchant_id).then(res => {
        this.categories = res.data
      }).catch(err => {
        window.console.log(err)
      })
    }
  },
  // watch:{
  //   categories: {
  //     deep: true,
  //     handler(){

  //     }
  //   }
  // },
  components: {
    [Tabs.name]:Tabs,
    [TabPane.name]:TabPane,
    [Form.name]:Form,
    [FormItem.name]:FormItem,
    [Input.name]:Input,
    [Upload.name]:Upload,
    [Button.name]:Button,
    [Dialog.name]:Dialog,
    [MessageBox.name]:MessageBox,
  }
}
</script>
