import axios from "axios"
import auth from "./auth"
import router from "../routes"

const BASE_URL = 'http://127.0.0.1:8000'
// const BASE_URL = 'http://www.ykjzone.com'

class Http{
  constructor(){
    this.http = axios.create({
      // baseURL，指定域名，请求的时候只指定path即可
      baseURL: BASE_URL,
      timeout: 10000,
      // headers不能在此指定，因为用户可能登录也可能没登录，在create中添加headers后不能在修改
    })

    // 请求之前的拦截器，用来设置JWT
    this.http.interceptors.request.use(config => {
      const token = auth.token
      if(token){
        config.headers.common.AUTHORIZATION = 'JWT ' + token
      }
      return config
    })

    // 请求之后的拦截器
    this.http.interceptors.response.use(null, err => {
      // 失败时执行
      if(err.response.status == 403){
        auth.clearUserToken()
        router.replace('/login')
        return Promise.reject(err)
      }
    })
  }
  /**
   * 所有请求
   */
  // 登录
  login(params){
    const url = '/cms/login'
    return this.http.post(url,params)
  }
  // 获取商家列表
  getMerchants(page){
    const url = '/cms/merchant?page=' + page
    return this.http.get(url)
  }
  // 获取商家
  getMerchant(merchant_id){
    const url = "/cms/merchant/" + merchant_id
    return this.http.get(url)
  }
  // 添加
  addMerchant(data){
    const url = '/cms/merchant'
    return this.http.post(url,data)
  }
  // 更新
  updateMerchant(merchant_id,data){
    const url = '/cms/merchant/' + merchant_id
    return this.http.put(url,data)
  }
  // 获取商品列表
  getCategories(merchant_id){
    const url = '/cms/category/merchant/' + merchant_id
    return this.http.get(url)
  }
  // 编辑分类提交
  updateCategory(id,data){
    const url = '/cms/category/' + id
    return this.http.put(url,data)
  }
  // 添加分类提交
  addCategory(data){
    const url = '/cms/category'
    return this.http.post(url,data)
  }
  // 删除分类
  deleteCategory(category_id){
    const url = "/cms/category/" + category_id
    return this.http.delete(url)
  }
  // 添加商品
  addGoods(data){
    const url = "/cms/goods"
    return this.http.post(url,data)
  }
  // 编辑商品
  updateGoods(id,data){
    const url = "/cms/goods/" + id
    return this.http.put(url,data)
  }
  // 删除商品
  deleteGoods(id){
    const url = "/cms/goods/" + id
    return this.http.delete(url)
  }
}

export default new Http()