import {Loading} from "element-ui"

class MTLoading{
  show(text=null){
    this.loading = Loading.service({
      fullscreen: true,
      text: text?text:'正在加载中...',
      spinner: 'el-icon-loading',
      background: "rgba(0,0,0,0.8)"
    })
  }
  hide(){
    if(this.loading){
      this.loading.close()
    }
  }
}

export default new MTLoading()