<style lang="scss" >
body{
  background-color: #eee;
}
.page-header{
  text-align: center;
  margin: 80px 0 40px;
}
</style>

<template>
  <div class="login-container">
    <el-row type="flex" justify="center">
      <el-col :span="6">
        <h1 class="page-header">美团管理系统</h1>
        <el-form ref="loginForm" :model="loginForm" label-width="80px" :rules="rules">
          <el-form-item label="用户名" prop="username">
            <el-input v-model="loginForm.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="loginForm.password" show-password></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">登录</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {Row,Col,Form,FormItem,Input,Button} from 'element-ui'

export default {
  name: 'Login',
  props: {
    
  },
  data(){
    // 自定义验证函数
    let validateTel = (rule, value, callback) => {
      if(!value.match(/1[3456789]\d{9}/)){
        callback(new Error("请输入正确的手机号码"))
      } else{
        callback()
      }
    }
    return {
      loginForm: {
        username:'',
        password:''
      },
      rules:{
        username:[
          {required:true, message:"请输入手机号码", trigger:'blur'},
          {validator:validateTel, trigger:'blur'}
        ],
        password:[
          {required:true, message:"请输入密码", trigger:'blur'},
          {min:6, max:20, message:"密码长度应该在6-20之间", trigger:'blur'}
        ]
      }
    }
  },
  methods: {
    onSubmit(){
      // 使用ref属性
      // window.console.log(this.$refs['loginForm'])
      // 判断是否已通过验证，valid: true通过，false不通过
      this.$refs['loginForm'].validate(valid => {
        if(!valid){
          // window.console.log('验证失败')
          return
        } 
        this.$loading.show()
        const params = {
          username: this.loginForm.username,
          password: this.loginForm.password
        }
        this.$http.login(params).then(res => {
          // 登录成功时执行
          const data = res.data
          const token = data.token
          const user = data.user
          this.$auth.setUserToken(user,token)
          this.$loading.hide()
          this.$router.push("/")
        }).catch(err => {
          // 登录失败时执行
          window.console.log(err)
          this.$loading.hide()
        })
      })
    }
  },
  components: {
    [Row.name]:Row,
    [Col.name]:Col,
    [Form.name]:Form,
    [FormItem.name]:FormItem,
    [Input.name]:Input,
    [Button.name]:Button,
  }
}
</script>


